import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { Button, Card, CardContent, Spacer } from 'react-neu'
import styled from 'styled-components'
import Web3 from 'web3'

import Split from 'components/Split'
import V3StakeModal from './components/V3StakeModal'

import useMediaQuery from 'hooks/useMediaQuery'
import useV3Farming from 'hooks/useV3Farming'
import useWallet from 'hooks/useWallet'
import useTransactionWatcher from 'hooks/useTransactionWatcher'

import { V3Farm } from 'constants/v3Farms'
import { exploreUrl, indexTokenAddress } from 'constants/ethContractAddresses'
import dateformat from 'dateformat'
import yoloLogo from 'assets/yolo-logo-black.png'
import { Props } from 'react-select'
import { yoloLink } from '../../../../constants/externalLinks'

const Stake: React.FC<Props> = (props:Props) => {
  const {chainId,token1,token2,tokenReward,tokenPair,pool,farms}=props.dstFarm
  // console.log("props.dstFarm",props.dstFarm)
  const { isMobile } = useMediaQuery()
  const { status, account, ethereum } = useWallet()
  const {
    onDeposit,
    onWithdraw,
    onClaimAccrued,
    getAllDepositedTokens,
    getAccruedRewardsAmount,
    getAllPendingRewardsAmount,
    getValidIds,
  } = useV3Farming()

  const { transactionStatus } = useTransactionWatcher()

  const [stakeModalIsOpen, setStakeModalIsOpen] = useState(false)
  const [accruedRewards, setAccruedRewards] = useState('0')
  const [allPendingRewards, setAllPendingRewards] = useState('0')
  const [validNfts, setValidNfts] = useState<number[]>([])
  const [depositedNfts, setDepositedNfts] = useState<number[]>([])

  const handleDismissStakeModal = useCallback(() => {
    setStakeModalIsOpen(false)
  }, [setStakeModalIsOpen])

  const handleOnStake = useCallback(
    (nftId: number, farm: V3Farm) => {
      onDeposit(nftId, farm)
      handleDismissStakeModal()
    },
    [handleDismissStakeModal, onDeposit]
  )

  const handleOnUnstake = useCallback(
    (nftId: number) => {
      onWithdraw(nftId, props.dstFarm)
    },
    [onWithdraw]
  )

  const handleStakeClick = useCallback(() => {
    setStakeModalIsOpen(true)
  }, [setStakeModalIsOpen])

  const handleClaimAccruedClick = useCallback(() => {
    if (!tokenReward.address) return

    onClaimAccrued(tokenReward.address)
  }, [onClaimAccrued])

  useEffect(() => {
    if (!tokenReward.address) return

    getAccruedRewardsAmount(tokenReward.address).then((amount) => {
      setAccruedRewards(
        parseFloat(Web3.utils.fromWei(amount?.toString() || '0')).toFixed(5)
      )
    })
  }, [account, status, transactionStatus, getAccruedRewardsAmount])

  useEffect(() => {
    getAllPendingRewardsAmount(props.dstFarm).then((amount) => {
      setAllPendingRewards(
        parseFloat(Web3.utils.fromWei(amount?.toString() || '0')).toFixed(5)
      )
    })
  }, [account, status, transactionStatus, getAllPendingRewardsAmount])

  useEffect(() => {
    getValidIds(props.dstFarm).then((idList) => {
      setValidNfts(idList || [])
    })
  }, [account, status, transactionStatus, getValidIds])

  useEffect(() => {
    getAllDepositedTokens(props.dstFarm).then((idList) => {
      setDepositedNfts(idList || [])
    })
  }, [account, status, transactionStatus, getAllDepositedTokens])

  const StakeButton = useMemo(() => {
    if (status !== 'connected') {
      return (
        <Button
          disabled
          full
          text='Stake & Unstake LP Tokens'
          variant='secondary'
        />
      )
    }
    return (
      <Button
        full
        onClick={handleStakeClick}
        text='Stake & Unstake LP Tokens'
      />
    )
  }, [status, handleStakeClick])

  const ClaimAccruedButton = useMemo(() => {
    if (status !== 'connected' || accruedRewards === '0.00000') {
      return (
        <Button
          disabled
          full
          text='Claim Accrued Rewards'
          variant='secondary'
        />
      )
    }

    return (
      <Button
        full
        onClick={handleClaimAccruedClick}
        text='Claim Accrued Rewards'
        variant='secondary'
      />
    )
  }, [status, handleClaimAccruedClick, accruedRewards])

  const ethDpiTokenIcon = (
    <StyledLpTokenWrapper>
      <StyledLpTokenImage
        alt={token1.symbol+'Icon'}
        src={token1.logoUrl}
      />
      <StyledLpTokenImage
        alt={token2.symbol+'Icon'}
        src={token2.logoUrl}
      />
    </StyledLpTokenWrapper>
  )

  return (
    <>
      <Card>
        <CardContent>
          <StyledCardTitleWrapper>
            {ethDpiTokenIcon}
            <Spacer size='md' />
            <StyledLmTitle>
              <StyledCardTitle>
                Yoloswap V3 {tokenPair} Liquidity Mining
              </StyledCardTitle>
              <Spacer size='sm' />
              <StyledCardSubtitle>
                {dateformat(farms[0].startTime * 1000)} -{' '}
                {dateformat(farms[0].endTime * 1000)}
              </StyledCardSubtitle>
              <Spacer size='sm' />
              <StyledCardSubtitle>
              <StyledLinkHeader href={exploreUrl+'/address/'+tokenReward.address} target='_blank' rel='noopener'>{tokenReward.symbol}</StyledLinkHeader>
              <StyledLinkHeader href={exploreUrl+'/address/'+token1.address} target='_blank' rel='noopener'>{token1.symbol}</StyledLinkHeader>
              <StyledLinkHeader href={exploreUrl+'/address/'+token2.address} target='_blank' rel='noopener'>{token2.symbol}</StyledLinkHeader>
              </StyledCardSubtitle>
            </StyledLmTitle>
          </StyledCardTitleWrapper>
          <Spacer />
          <StyledFarmTokensAndApyWrapper>
            <Split>
              <div>
                <StyledFarmText>{depositedNfts.length}</StyledFarmText>
                <StyledSectionLabel>Staked Yoloswap V3 NFTs</StyledSectionLabel>
              </div>
              <div>
                <StyledFarmText>
                  {allPendingRewards}
                  <StyledTokenIcon
                    alt={tokenReward.symbol+'Icon'}
                    src={tokenReward.logoUrl}
                  />
                </StyledFarmText>
                <StyledSectionLabel>Pending Rewards</StyledSectionLabel>
              </div>

              <div>
                <StyledFarmText>
                  {accruedRewards}
                  <StyledTokenIcon
                    alt={tokenReward.symbol+'Icon'}
                    src={tokenReward.logoUrl}
                  />
                </StyledFarmText>
                <StyledSectionLabel>Accrued Rewards</StyledSectionLabel>
              </div>
            </Split>
          </StyledFarmTokensAndApyWrapper>
        </CardContent>
        <StyledCardActions isMobile={isMobile}>
          {StakeButton}
          <Spacer />
          {ClaimAccruedButton}
        </StyledCardActions>
      </Card>
      <V3StakeModal
        isOpen={stakeModalIsOpen}
        availableNftIds={validNfts}
        depositedNftIds={depositedNfts}
        farm={props.dstFarm}
        provider={ethereum}
        accruedRewards={accruedRewards}
        onDismiss={handleDismissStakeModal}
        onStake={handleOnStake}
        onUnstake={handleOnUnstake}
      />
    </>
  )
}

const StyledTokenIcon = styled.img`
  height: 20px;
  margin-left: 10px;
`

const StyledCardTitle = styled.span`
  font-weight: 600;
  font-size: 28px;
`

const StyledCardSubtitle = styled.span`
  color: ${(props) => props.theme.colors.grey[500]};
  font-weight: 600;
  font-size: 18px;
`

const StyledCardTitleWrapper = styled.div`
  display: flex;
  align-items: center;

  flex-wrap: wrap;
  font-weight: 600;
  font-size: 24px;
`

const StyledLmTitle = styled.div`
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 24px;
`

const StyledFarmTokensAndApyWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
`

const StyledFarmText = styled.span`
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 24px;
`

const StyledSectionLabel = styled.span`
  color: ${(props) => props.theme.colors.grey[500]};
  font-size: 16px;
`

interface StyledCardActionProps {
  isMobile: boolean
}

const StyledCardActions = styled.div<StyledCardActionProps>`
  display: flex;
  flex-wrap: ${(props) => (props.isMobile ? 'wrap' : 'no-wrap')};
  padding: 30px;
  padding-top: 0px;
`

const StyledLpTokenWrapper = styled.div`
  margin-left: 10px;
`

const StyledLpTokenImage = styled.img`
  height: 50px;
  margin-left: -10px;
`

const StyledLinkHeader = styled.a`
  color: ${props => props.theme.colors.grey[500]};
  font-weight: 600;
  font-size: 18px;
  padding-left: ${props => props.theme.spacing[0]}px;
  padding-right: ${props => props.theme.spacing[5]}px;
  text-decoration: underline;
  &:hover {
    color: ${(props) => props.theme.colors.white};
  }
`
export default Stake
