import React, { useCallback, useEffect, useState } from 'react'
import BigNumber from 'utils/bignumber'
import { provider } from 'web3-core'

import Context from './Context'
import useWallet from 'hooks/useWallet'
import { getBalance, getEthBalance } from 'utils/index'
import { getEarnedIndexTokenQuantity } from 'index-sdk/stake'
import { getEarnedIndexTokenQuantity as getEarnedFarmTwoBalance } from 'index-sdk/farmTwo'
import { getEarnedIndexTokenQuantity as getMviRewardsBalance } from 'index-sdk/mviStaking'
import {
  yoloTokenAddress,
  pddTokenAddress,
  jdTokenAddress,
} from 'constants/ethContractAddresses'

const Provider: React.FC = ({ children }) => {
  const [ethBalance, setEthBalance] = useState<BigNumber>()
  const [yoloBalance, setYoloBalance] = useState<BigNumber>()
  const [pddBalance, setPddBalance] = useState<BigNumber>()
  const [jdBalance, setJdBalance] = useState<BigNumber>()

  const {
    account,
    ethereum,
    status,
  }: {
    account: string | null | undefined
    ethereum: provider
    status: string
  } = useWallet()

  const fetchBalances = useCallback(
    async (userAddress: string, provider: provider) => {

      let condition:boolean=!yoloTokenAddress ||!pddTokenAddress ||!jdTokenAddress

      if (condition) {
        throw new Error("A token address is not defined. Please check your .env to confirm all token addresses are defined.")
      }

      const balances = await Promise.all([
        getEthBalance(provider, userAddress),
        getBalance(provider, yoloTokenAddress, userAddress),
        getBalance(provider, pddTokenAddress, userAddress),
        getBalance(provider, jdTokenAddress, userAddress),
      ])


      // console.log("balances",balances)

      setEthBalance(new BigNumber(balances[0]))
      setYoloBalance(new BigNumber(balances[1]))
      setPddBalance(new BigNumber(balances[2]))
      setJdBalance(new BigNumber(balances[3]))
    },
    [
      setEthBalance,
      setYoloBalance,
      setPddBalance,
      setJdBalance,
    ]
  )

  useEffect(() => {
    if (status !== 'connected') {
      setEthBalance(new BigNumber(0))
      setYoloBalance(new BigNumber(0))
      setPddBalance(new BigNumber(0))
      setJdBalance(new BigNumber(0))
    }
  }, [status])

  useEffect(() => {
    if (account && ethereum) {
      fetchBalances(account, ethereum)
      let refreshInterval = setInterval(
        () => fetchBalances(account, ethereum),
        10000
      )
      return () => clearInterval(refreshInterval)
    }
  }, [account, ethereum, fetchBalances])

  return (
    <Context.Provider
      value={{
        ethBalance,
        yoloBalance,
        pddBalance,
        jdBalance,
      }}
    >
      {children}
    </Context.Provider>
  )
}

export default Provider
